:root {
  --nn-first-color: red;
  --nn-second-color: orange;
  --nn-third-color: yellow;
}

.gradient-text {
  background-color: red;
  background-image: linear-gradient(to right, red, orange, var(--nn-third-color, yellow), orange, red);
  background-repeat: repeat;
  background-size: 100%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
  animation-name: example;
  animation-duration: 2s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
  display: inline-block;
}

.loader-header {
  text-align: center;
  margin-top: calc(40vh - 3em);
}

.loader-text {
  text-align: center;
  margin: auto;
  font-size: calc(4rem + 1.5vw);
  font-weight: bold;
}

@keyframes example {
  0% {
    background-image: linear-gradient(to right, var(--nn-first-color, red), var(--nn-second-color, orange), var(--nn-third-color, yellow), var(--nn-third-color, yellow), var(--nn-second-color, orange), var(--nn-first-color, red));
  }

  20% {
    background-image: linear-gradient(to right, var(--nn-second-color, orange), var(--nn-third-color, yellow), var(--nn-third-color, yellow), var(--nn-second-color, orange), var(--nn-first-color, red), var(--nn-first-color, red));
  }

  40% {
    background-image: linear-gradient(to right, var(--nn-third-color, yellow), var(--nn-third-color, yellow), var(--nn-second-color, orange), var(--nn-first-color, red), var(--nn-first-color, red), var(--nn-second-color, orange));
  }

  60% {
    background-image: linear-gradient(to right, var(--nn-third-color, yellow), var(--nn-second-color, orange), var(--nn-first-color, red), var(--nn-first-color, red), var(--nn-second-color, orange), var(--nn-third-color, yellow));
  }

  80% {
    background-image: linear-gradient(to right, var(--nn-second-color, orange), var(--nn-first-color, red), var(--nn-first-color, red), var(--nn-second-color, orange), var(--nn-third-color, yellow), var(--nn-third-color, yellow));
  }

  100% {
    background-image: linear-gradient(to right, var(--nn-first-color, red), var(--nn-first-color, red), var(--nn-second-color, orange), var(--nn-third-color, yellow), var(--nn-third-color, yellow), var(--nn-second-color, orange));
  }
}

